import React, {FC, memo, useCallback, useMemo, useState} from 'react';


import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import {Divider} from '@mui/material';
import MuiAccordion, {AccordionProps} from '@mui/material/Accordion';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import MuiAccordionSummary, {
  AccordionSummaryProps,
} from '@mui/material/AccordionSummary';
import Chip from '@mui/material/Chip';
import {SelectChangeEvent} from '@mui/material/Select';
import {styled} from '@mui/material/styles';
import Typography from '@mui/material/Typography';

import {BranchProps} from '../pages/branches';
import {CollegeDetailsProps} from '../templates/college-details';



const Accordion = styled((props: AccordionProps) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({theme}) => ({
  border               : `1px solid ${theme.palette.divider}`,
  '&:not(:last-child)' : {
    borderBottom : 0,
  },
  '&:before' : {
    display : 'none',
  },
}));
  

const AccordionSummary = styled((props: AccordionSummaryProps) => (
  <MuiAccordionSummary expandIcon={<ExpandMoreIcon />}
    {...props}/>
))(({theme}) => ({
//   backgroundColor :
//       theme.palette.mode === 'dark'
//         ? 'rgba(255, 255, 255, .05)'
//         : 'rgba(0, 0, 0, .03)',
  //   flexDirection                                           : 'row-reverse',
  //   '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded' : {
  //     transform : 'rotate(90deg)',
  //   },
  '& .MuiAccordionSummary-content' : {
    marginLeft : theme.spacing(1),
  },
}));
  
const AccordionDetails = styled(MuiAccordionDetails)(({theme}) => ({
  padding : theme.spacing(2),
//   borderTop : '1px solid rgba(0, 0, 0, .125)',
}));

type Props = {
    colleges: CollegeDetailsProps[];
    selectedBranches?: BranchProps[];
}

const CollegeDetailsCard: FC<Props> = ({
  colleges,
  selectedBranches,
}) => {
  // console.log(colleges,selectedBranches);
  return (
    <>
      {colleges?.map(college => (
        <CollegeDetailsAccordion key={college.id}
          college={college}
          selectedBranches={selectedBranches} />
      ))}
    </>
  );
};

export default memo(CollegeDetailsCard);

type CollegeDetailsAccordionProps = {
    college: CollegeDetailsProps
    selectedBranches?: BranchProps[];
}
const CollegeDetailsAccordion: FC<CollegeDetailsAccordionProps> = ({
  college,
  selectedBranches,
}) => {
  const [selectedBranch, setSelectedBranch] = useState('');
  const handleChange = useCallback((event: SelectChangeEvent) => {
    setSelectedBranch(event.target.value as string);
  }, []);
  // console.log(college)
  // console.log(selectedBranches)

  const searchCollegesByBranch = useMemo(() => {
    const matchedObjects = new Set();
  
    selectedBranches?.forEach(searchItem => {
      college.branches.forEach(branch => {
        if (branch.code === searchItem.code) {
          matchedObjects.add(branch);
        }
      });
    });
    
    return Array.from(matchedObjects);
  }, [selectedBranches, college.branches]);

  const displayBranchNames = useMemo(() => searchCollegesByBranch.map(({name}: any) => name).join(', '), [searchCollegesByBranch]);
  return (
    <Accordion TransitionProps={{unmountOnExit: true}}
      key={college.id}>
      <AccordionSummary id={college.id}>
        <div>
          <Typography variant="caption" display="block" gutterBottom>
        Code: {college.code}
          </Typography>
          <Typography gutterBottom>{college.name} - {college.city}</Typography>
          <Typography variant="body2" gutterBottom>{displayBranchNames}</Typography>
        </div>
      </AccordionSummary>
      <AccordionDetails>

        {!!college?.college_type?.name && (
          <Chip label={college.college_type.name}
            size="small"
            sx={{mb: 2}}/>
        )}
        <Typography variant="body1" gutterBottom component="div">
          {college.address}
        </Typography>
        <Divider light sx={{my: 2}} />
        {college.branches?.map(({id, name}) => (
          <Typography key={id} gutterBottom>{name}</Typography>
        ))}

        {/* <Button size="small" component={Link} to={`/${college.code}`}>
              View Colleges
      </Button> */}
        {/* <FormControl fullWidth variant="standard">
          <InputLabel>Choose Branch</InputLabel>
          <Select value={selectedBranch}
            label="Choose Branch"
            onChange={handleChange}>
            {college.branches?.map(branch => (
              <MenuItem key={branch.id} value={branch.id}>{branch.name}</MenuItem>
            ))}
          </Select>
        </FormControl> */}
      </AccordionDetails>
    </Accordion>
  );
};